<template>
  <div>
    <div style="height:50px"></div>
    <!-- 底部导航栏  用vant插件 Tabbar   配置好路由-->
    <van-tabbar v-model="active" active-color="salmon" inactive-color="#000">
      <van-tabbar-item icon="wap-home-o" to="/home">首页</van-tabbar-item>
      <van-tabbar-item icon="apps-o" to="/cartgory">分类</van-tabbar-item>
      <van-tabbar-item icon="shopping-cart-o" to="/car">购物车</van-tabbar-item>
      <van-tabbar-item icon="user-circle-o" to="/my">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 0
    };
  },
  watch: {
    $route: function() {
      if (this.$route.path == "/my") {
        let gg = localStorage.getItem("_TNK");
        if (gg) {
        } else {
          this.$router.push({ name: "Register" });
        }
      }
      switch (this.$route.path) {
        case "/home":
          this.active = 0;
          break;
        case "/cartgory":
          this.active = 1;
          break;
        case "/car":
          this.active = 2;
          break;
        case "/my":
          this.active = 3;
          break;
      }
    }
  }
};
</script>