import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import VueAxios from 'vue-axios'

//图片懒加载
import Vuelazyload from "vue-lazyload"

//注册    //使用占位符
Vue.use(Vuelazyload,{
  loading:require("../src/assets/img/timg.jpg")
})

//导入vant框架
import {
  Search, Swipe, SwipeItem, Grid, GridItem, Sticky, Tabbar, TabbarItem, TreeSelect, NavBar, SwipeCell ,Empty,
  Button,
  Checkbox,
  SubmitBar,
  Stepper,
  Cell,
  CellGroup,
  Icon ,
  GoodsAction,
  GoodsActionButton,
  GoodsActionIcon,
  ActionSheet,
  Sku,
  Form,
  Field,
  Toast,
  AddressEdit,
  AddressList,
  Popup,
  Uploader,
  Dialog} from 'vant'

//注册vant框架的组件
Vue
  .use(Search)
  .use(Swipe)
  .use(SwipeItem)
  .use(Grid)
  .use(GridItem)
  .use(Sticky)
  .use(Tabbar)
  .use(TabbarItem)
  .use(TreeSelect)
  .use(NavBar)
  .use(Button)
  .use(Stepper)
  .use(Empty)
  .use(SwipeCell)
  .use(Checkbox)
  .use(SubmitBar)
  .use(Cell)
  .use(CellGroup)
  .use(Icon )
  .use(GoodsAction)
  .use(GoodsActionButton)
  .use(GoodsActionIcon)
  .use(ActionSheet)
  .use(Sku)
  .use(Form)
  .use(Field)
  .use(Toast)
  .use(AddressEdit)
  .use(AddressList)
  .use(Popup)
  .use(Uploader)
  .use(Dialog)
//引入 rem 适配基准模式
import "lib-flexible"



//注册axios
Vue.use(VueAxios, axios)

//配置请求基础路径
axios.defaults.baseURL = 'http://www.kangliuyong.com:10002'

//配置post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';

//发起请求之前处理
axios.interceptors.request.use(config => {
  // 

  if (config.method == 'post') {
    let paramsString = '';
    //post请求参数序列化，转成一个字符串
    for (let key in config.data) {
      paramsString += key + '=' + config.data[key] + '&'
    }

    // 

    config.data = paramsString.slice(0, -1);

    // 
  }

  return config;
})

//添加appkey属性保存appkey
Vue.prototype.appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';



Vue.config.productionTip = false

Vue.prototype.$bus = new Vue()


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
