export const mutations = {

    //加入购物车
    getshopdata(state,payload){
        payload.isActive = false
        var agg = true
        if(state.shopnum.length == 0){
            agg = false
            state.shopnum.unshift(payload)
        }else{
            state.shopnum.forEach(v => {

                if (v.stockId == payload.stockId) {
                    v.count += payload.count
                    agg = false
                }
            })
            
        }
        if (agg){
            state.shopnum.unshift(payload)
        }
        

    },
    editActive(state,vaule){
        state.shopnum.forEach(v=>{
            v.isActive = vaule
        })
        
    },

    disabled(state,val){
        
        state.isNotHasCheck = !val
    },

    disableds(state){
        state.isNotHasCheck = true
        for (let i = 0; i < state.shopnum.length; i++) {
            if (state.shopnum[i].isActive){
                state.isNotHasCheck = false
                break
            }
            
        }
    },

    gwcnum(state,item){
        for (let i = 0; i < state.shopnum.length; i++) {
            if (state.shopnum[i].stockId == item.stockId) {
                state.shopnum[i].count = item.count
                break
            }

        }
        
    },

    //删除单个
    remove(state,index){
        state.shopnum.splice(index,1)
    },

    //删除多个
    removemuch(state){
        
        state.shopnum = []
    },

    //临时保存
    addshop(state,vaule){
        state.shop = vaule
    },

    deleteshop(state){
        state.shop =[]
    },

    //加入订单中
    addDingdan(state,vaule){
        vaule.forEach(v=>{
            v.isActive = true
        })
        state.dingdan.unshift(...vaule)
        state.waitshop.unshift(...vaule)
    },

    //当在购物车加入时删除商品
    deleteshop(state,vaule){
        state.shopnum.shift(vaule)
    },


    //确认收货修改
    comfirms(state,vaule){

        state.dingdan.forEach(v=>{
            if(v.stockId == vaule){
                v.isActive = false
            }
        })
        
        state.waitshop.forEach(o => {
            if (o.stockId == vaule) {
                state.waitshop.shift(o)
                state.received.unshift(o)
            }
        })
    },

    //监听tab事件
    tab(state,index){
        switch (index) {
            case 0:
                state.current = "pop";
                break;
            case 1:
                state.current = "new";
                break;
            case 2:
                state.current = "sell";
        }
    },
    Height(state,value){
        state.height = value
        
    }

}