export const routes = [
    {
        path: "/cartgory",
        name: "Cartgory",
        component: r => require(['../views/cartgory/Cartgory.vue'], r),
        meta: {
            keepAlive: true, //此组件需要被缓存
        }
    },
    {
        path: '/home',
        name: 'Home',
        component: r => require(['../views/Home.vue'], r),
        meta: {
            keepAlive: true, //此组件需要被缓存
        }
    },
    {
        path: "/car",
        name: "Car",
        component: r => require(['../views/car/Car.vue'], r),
        meta: {
            keepAlive: true, //此组件需要被缓存
        }
    },
    {
        path: "/my",
        name: "My",
        component: r => require(['../views/my/My.vue'], r),
        meta: {
            keepAlive: true, //此组件需要被缓存
        }
    },
    {
        path: "/detail",
        name: "Detail",
        component: r => require(['../views/detail/Detail.vue'], r),
        meta: {
            keepAlive: false, //此组件需要被缓存
        }
    },
    {
        path: '/register',
        name: 'Register',
        component: r => require(['../views/Register.vue'], r)
    },
    {
        path: '/login',
        name: 'Login',
        component: r => require(['../views/Login.vue'], r)
    },
    {
        path: '/gocar',
        name: 'Gocar',
        component: r => require(['../views/car/Gocar.vue'], r)
    },
    {
        path: '/address/:aid?',
        name: 'Address',
        component: r => require(['../views/address/Address.vue'], r)
    },
    {
        path: '/myaddress',
        name: 'MyAddress',
        component: r => require(['../views/address/MyAddress.vue'], r)
    },
    {
        path: '/order',
        name: 'Order',
        component: r => require(['../views/order/Order.vue'], r)
    },
    {
        path: '/wait',
        name: 'Wait',
        component: r => require(['../views/order/Wait.vue'], r)
    },
    {
        path: '/received',
        name: 'Received',
        component: r => require(['../views/order/Received.vue'], r)
    },
    {
        path: '/account',
        name: 'Account',
        component: r => require(['../views/myname/Account.vue'], r)
    },
    {
        path: '/secure',
        name: 'Secure',
        component: r => require(['../views/myname/Secure.vue'], r)
    },
    {
        path: '/contact',
        name: 'Contact',
        component: r => require(['../views/myname/Contact.vue'], r)
    },
    {
        path: '*',
        redirect: {
            name: 'Home'
        }
    }
]